import React, { FC, useState } from 'react';
import { Modal, Button, Divider, Typography } from 'antd';
import { Moment } from 'moment';
import { KeyOutlined } from '@ant-design/icons';
import {
    formatListingPrice,
    CreateBooking as CreateBookingData,
    CreateBookingVariables,
    CREATE_BOOKING,
    displayErrorMessage,
    displaySuccessNotification,
} from '../../../../lib';
import { useStripe, CardElement, useElements } from '@stripe/react-stripe-js';
import { useMutation } from 'react-apollo';

interface Props {
    handleListingRefetch: () => Promise<void>;
    clearBookingData: () => void;
    listingId: string;
    price: number;
    modalVisible: boolean;
    checkInDate: Moment;
    checkOutDate: Moment;
    setModalVisible: (modalVisible: boolean) => void;
}
const { Paragraph, Text, Title } = Typography;
export const ListingCreateBookingModal: FC<Props> = ({
    handleListingRefetch,
    clearBookingData,
    modalVisible,
    setModalVisible,
    price,
    checkInDate,
    checkOutDate,
    listingId,
}) => {
    const [disableBookingButton, setDisableBookingButton] = useState(false);
    const daysBooked = checkOutDate.diff(checkInDate, 'days') + 1;
    const listingPrice = price * daysBooked;
    const tinyHouseFee = 0.05 * listingPrice;
    const totalPrice = Math.ceil(
        (listingPrice + tinyHouseFee + 30) / (1 - 0.029)
    );

    const stripe = useStripe();
    const elements = useElements();
    const [createBooking, { loading }] = useMutation<
        CreateBookingData,
        CreateBookingVariables
    >(CREATE_BOOKING, {
        onCompleted: () => {
            clearBookingData();
            handleListingRefetch();
            displaySuccessNotification(
                "You've successfully booked the listing!",
                'Booking history can always be found in your User page.'
            );
            setDisableBookingButton(false);
        },
        onError: () => {
            displayErrorMessage(
                "Sorry! We weren't able to book the listing. Please try again later!"
            );
            setDisableBookingButton(false);
        },
    });

    const handleCreateBooking = async () => {
        if (!stripe || !elements) {
            return displayErrorMessage(
                "Sorry! We weren't able to connect with Stripe."
            );
        }
        setDisableBookingButton(true);
        const cardElement = elements.getElement(CardElement);

        if (cardElement) {
            const { token, error } = await stripe.createToken(cardElement);
            if (token) {
                createBooking({
                    variables: {
                        input: {
                            source: token.id,
                            checkIn: checkInDate.format('YYYY-MM-DD'),
                            checkOut: checkOutDate.format('YYYY-MM-DD'),
                            id: listingId,
                        },
                    },
                });
            } else {
                displayErrorMessage(
                    error && error.message
                        ? error.message
                        : "Sorry! We weren't able to book this listing. Please try again later."
                );
                setDisableBookingButton(false);
            }
            console.log(token);
        } else {
            setDisableBookingButton(false);

            return displayErrorMessage(
                "Sorry! We weren't able to book this listing. Please try again later."
            );
        }
    };

    return (
        <Modal
            visible={modalVisible}
            centered
            footer={null}
            onCancel={() => setModalVisible(false)}
        >
            <div className="listing-booking-modal">
                <div className="listing-booking-modal__intro">
                    <Title className="listing-booking-modal__intro-title">
                        <KeyOutlined />
                    </Title>
                    <Title
                        level={3}
                        className="listing-booking-modal__intro-title"
                    >
                        Book your trip
                    </Title>
                    <Paragraph>
                        Enter your payment information to book the listing from
                        the dates between{' '}
                        <Text strong mark>
                            {checkInDate.format('MMMM Do YYYY')}
                        </Text>{' '}
                        and{' '}
                        <Text strong mark>
                            {checkOutDate.format('MMMM Do YYYY')}
                        </Text>{' '}
                        (inclusive)
                    </Paragraph>
                </div>
                <Divider />

                <div className="listing-booking-modal__charge-summary">
                    <Paragraph>
                        {formatListingPrice(price, false)} * {daysBooked} days ={' '}
                        <Text strong>
                            {formatListingPrice(listingPrice, false)}
                        </Text>
                    </Paragraph>
                    <Paragraph>
                        Estimated Fees ={' '}
                        <Text strong>
                            {formatListingPrice(totalPrice - listingPrice)}
                        </Text>
                    </Paragraph>
                    <Paragraph className="listing-booking-modal__charge-summary-total">
                        Total ={' '}
                        <Text mark>{formatListingPrice(totalPrice)}</Text>
                    </Paragraph>
                </div>
                <Divider />

                <div className="listing-booking-modal__stripe-card-section">
                    <CardElement
                        className="listing-booking-modal__stripe-card"
                        options={{ hidePostalCode: true }}
                    />
                    <Button
                        loading={loading}
                        disabled={disableBookingButton}
                        size="large"
                        type="primary"
                        className="listing-booking-modal__cta"
                        onClick={handleCreateBooking}
                    >
                        Book
                    </Button>
                </div>
            </div>
        </Modal>
    );
};
