import React, { FC } from 'react';
import { Button, Menu } from 'antd';
import { HomeTwoTone, UserOutlined, LogoutOutlined } from '@ant-design/icons';
import { Link } from 'react-router-dom';
import {
    Viewer,
    LOG_OUT,
    LogOut as LogOutData,
    displaySuccessNotification,
    displayErrorMessage,
} from '../../../../lib';

import { useMutation } from 'react-apollo';
const { Item, SubMenu } = Menu;

interface Props {
    viewer: Viewer;
    setViewer: (viewer: Viewer) => void;
}

export const MenuItems: FC<Props> = ({ viewer, setViewer }) => {
    const [logOut] = useMutation<LogOutData>(LOG_OUT, {
        onCompleted: (data) => {
            if (data?.logOut) {
                setViewer(data.logOut);
                displaySuccessNotification(
                    "You've successfully logged out, come back soon!"
                );
                sessionStorage.removeItem('token');
            }
        },
        onError: (data) => {
            displayErrorMessage(
                "Sorry! We weren't able to log you out. Please try again later."
            );
        },
    });

    const handleLogout = () => {
        console.log('logging out...');
        logOut();
    };

    const subMenuLogin =
        viewer.id && viewer.avatar ? (
            <SubMenu
                title={
                    <img
                        alt="user avatar"
                        src={viewer.avatar}
                        referrerPolicy="no-referrer"
                        style={{ width: 48, height: 48, borderRadius: '50%' }}
                    />
                }
            >
                <Item key="/user">
                    <Link to={`/user/${viewer.id}`}>
                        <UserOutlined />
                        Profile
                    </Link>
                </Item>
                <Item key="/logout" onClick={handleLogout}>
                    <LogoutOutlined />
                    Logout
                </Item>
            </SubMenu>
        ) : (
            <Item>
                <Link to="/login">
                    <Button type="primary">Sign In</Button>
                </Link>
            </Item>
        );

    return (
        <Menu mode="horizontal" selectable={false} className="menu">
            <Item key="/host">
                <Link to="/host">
                    <HomeTwoTone />
                </Link>
                Host
            </Item>
            {subMenuLogin}
        </Menu>
    );
};
