import React, { FC, useState, useEffect, useRef } from 'react';
import { useQuery } from 'react-apollo';
import { Layout, List, Affix } from 'antd';
import {
    ListingCard,
    LISTINGS,
    Listings as ListingsData,
    ListingsVariables,
    ErrorBanner,
} from '../../lib';
import { ListingsFilter } from '../../lib/graphql/globalTypes';
import { RouteComponentProps } from 'react-router';
import Title from 'antd/lib/typography/Title';
import Paragraph from 'antd/lib/typography/Paragraph';
import Text from 'antd/lib/typography/Text';
import { Link } from 'react-router-dom';
import {
    ListingsSkeleton,
    ListingsFilters,
    ListingsPagination,
} from './components';

interface MatchParams {
    location?: string;
}

const { Content } = Layout;

const PAGE_LIMIT = 8;
export const Listings: FC<RouteComponentProps<MatchParams>> = ({ match }) => {
    const locationRef = useRef(match.params.location);
    const [filter, setFilter] = useState(ListingsFilter.PRICE_LOW_TO_HIGH);
    const [page, setPage] = useState(1);
    const { data, loading, error } = useQuery<ListingsData, ListingsVariables>(
        LISTINGS,
        {
            skip: page !== 1 && locationRef.current !== match.params.location,
            variables: {
                location: match.params.location,
                filter,
                limit: PAGE_LIMIT,
                page: page,
            },
        }
    );

    useEffect(() => {
        setPage(1);
        locationRef.current = match.params.location;
    }, [match.params.location]);

    if (loading) {
        return (
            <Content className="listings">
                <ListingsSkeleton />
            </Content>
        );
    }

    if (error)
        return (
            <Content className="listings">
                <ErrorBanner description="We either couldn't find anything matching your search or have encountered an error. If you're searching for a unique location, try searching again with more common keywords." />
                <ListingsSkeleton />
            </Content>
        );

    const listings = data?.listings;

    const listingsRegion = listings?.region;
    const listingsRegionElement = listingsRegion && (
        <Title level={3} className="listings__title">
            Results for "{listingsRegion}"
        </Title>
    );

    const listingsSectionElement =
        listings && listings.result.length ? (
            <div>
                <Affix offsetTop={64}>
                    <div>
                        <ListingsPagination
                            total={listings.total}
                            page={page}
                            limit={PAGE_LIMIT}
                            setPage={setPage}
                        />
                        <ListingsFilters
                            filter={filter}
                            setFilter={setFilter}
                        />
                    </div>
                </Affix>
                <List
                    grid={{
                        gutter: 8,
                        xs: 1,
                        sm: 2,
                        md: 2,
                        lg: 4,
                        xl: 4,
                        xxl: 4,
                    }}
                    dataSource={listings.result}
                    renderItem={(listing) => (
                        <List.Item>
                            <ListingCard listing={listing} />
                        </List.Item>
                    )}
                />
            </div>
        ) : (
            <div>
                <Paragraph>
                    It appears that no listings have yet been created for{' '}
                    <Text mark>"{listingsRegion}"</Text>
                </Paragraph>
                <Paragraph>
                    Be the first person to create a{' '}
                    <Link to="/host">listing in this area</Link>
                </Paragraph>
            </div>
        );

    return (
        <Content className="listings">
            {listingsRegionElement}
            {listingsSectionElement}
        </Content>
    );
};
