import React, { FC } from 'react';
import { Card, Typography, Divider, Button, DatePicker } from 'antd';
import {
    formatListingPrice,
    displayErrorMessage,
    Viewer,
    Listing as ListingData,
} from '../../../../lib';
import moment, { Moment } from 'moment';
import Text from 'antd/lib/typography/Text';
import { BookingsIndex } from './types';

const { Title, Paragraph } = Typography;

interface Props {
    setModalVisible: (modalVisible: boolean) => void;
    price: number;
    checkInDate: Moment | null;
    checkOutDate: Moment | null;
    setCheckInDate: (date: Moment | null) => void;
    setCheckOutDate: (date: Moment | null) => void;
    viewer: Viewer;
    host: ListingData['listing']['host'];
    bookingsIndex: ListingData['listing']['bookingsIndex'];
}

export const ListingCreateBooking: FC<Props> = ({
    bookingsIndex,
    host,
    viewer,
    price,
    checkInDate,
    checkOutDate,
    setCheckInDate,
    setCheckOutDate,
    setModalVisible,
}) => {
    const bookingsIndexJSON: BookingsIndex = bookingsIndex
        ? JSON.parse(bookingsIndex)
        : null;

    const dateIsBooked = (currentDate: Moment) => {
        const y = currentDate.year();
        const m = currentDate.month();
        const d = currentDate.date();

        return (
            bookingsIndexJSON[y] &&
            bookingsIndexJSON[y][m] &&
            bookingsIndexJSON[y][m][d]
        );
    };

    const disabledDate = (currentDate?: Moment) => {
        if (currentDate) {
            const dateIsBeforeEndOfDay = currentDate.isBefore(
                moment().endOf('day')
            );
            return dateIsBooked(currentDate) || dateIsBeforeEndOfDay;
        } else {
            return false;
        }
    };

    const verifyCheckoutDates = (selectedCheckOutDate: Moment | null) => {
        if (checkInDate && selectedCheckOutDate) {
            if (moment(selectedCheckOutDate).isBefore(checkInDate, 'days')) {
                return displayErrorMessage(
                    "You can't book date of check out to be prior to check in!"
                );
            }
            const dateCursor = moment(checkInDate);
            while (dateCursor.isBefore(selectedCheckOutDate, 'days')) {
                dateCursor.add(1, 'days');

                const y = dateCursor.year();
                const m = dateCursor.month();
                const d = dateCursor.date();

                if (
                    bookingsIndexJSON[y] &&
                    bookingsIndexJSON[y][m] &&
                    bookingsIndexJSON[y][m][d]
                ) {
                    setCheckOutDate(null);
                    return displayErrorMessage(
                        "You can't book a preiod of time that overlaps existing bookings. Please try again!"
                    );
                }
            }
            console.log('hi');
            setCheckOutDate(selectedCheckOutDate);
        }
    };

    const viewerIsHost = viewer.id === host.id;
    const checkInInputDisabled = !viewer.id || viewerIsHost || !host.hasWallet;
    const checkOutInputDisabled = checkInInputDisabled || !checkInDate;
    const buttonDisabled =
        checkOutInputDisabled || !checkInDate || !checkOutDate;

    let buttonMessage = "You won't be charged yet";
    if (!host.hasWallet)
        buttonMessage =
            "The host has disconnected from Stripe and thus won't be able to receive payments";
    if (!viewer.id) {
        buttonMessage = 'You must be signed in in order to book a listing';
    }
    if (viewerIsHost) buttonMessage = 'You cannot book your own listing';

    return (
        <div className="listing-booking">
            <Card
                className="listing-book__card"
                style={{ textAlign: 'center' }}
            >
                <div>
                    <Paragraph>
                        <Title
                            level={2}
                            className="listing-booking__card-title"
                        >
                            {formatListingPrice(price)}
                            <span>/day</span>
                        </Title>
                    </Paragraph>
                    <Divider />
                    <div className="listing-booking__card-date-picker">
                        <Paragraph strong>Check In</Paragraph>
                        <DatePicker
                            value={checkInDate ? checkInDate : undefined}
                            onChange={(checkInDate) =>
                                setCheckInDate(checkInDate)
                            }
                            format={'YYYY/MM/DD'}
                            disabled={checkInInputDisabled}
                            disabledDate={disabledDate}
                            showToday={false}
                            onOpenChange={() => setCheckOutDate(null)}
                        />
                    </div>
                    <div className="listing-booking__card-date-picker">
                        <Paragraph strong>Check Out</Paragraph>
                        <DatePicker
                            value={checkOutDate ? checkOutDate : undefined}
                            onChange={(checkOutDate) =>
                                verifyCheckoutDates(checkOutDate)
                            }
                            format={'YYYY/MM/DD'}
                            disabledDate={disabledDate}
                            showToday={false}
                            disabled={checkOutInputDisabled}
                        />
                    </div>
                </div>
                <Divider />
                <Button
                    onClick={() => setModalVisible(true)}
                    size="large"
                    type="primary"
                    className="listing-booking__card-cta"
                    disabled={buttonDisabled}
                >
                    Request to book!
                </Button>
                <Text type="secondary" mark>
                    {buttonMessage}
                </Text>
            </Card>
        </div>
    );
};
