import React, { FC } from 'react';
import { HomeHero, HomeListings, HomeListingsSkeleton } from './components';
import { Col, Row, Layout, Typography } from 'antd';
import { RouteComponentProps, Link } from 'react-router-dom';
import mapBackgroud from './assets/map-background.jpg';
import {
    displayErrorMessage,
    LISTINGS,
    Listings as ListingsData,
    ListingsVariables,
} from '../../lib';
import sanFranciscoImage from './assets/san-fransisco.jpg';
import cancunImage from './assets/cancun.jpg';
import { useQuery } from 'react-apollo';
import { ListingsFilter } from '../../lib/graphql/globalTypes';

const { Content } = Layout;
const { Title, Paragraph } = Typography;

const PREMIUM_LISTINGS_LIMIT = 4;
const PREMIUM_LISTINGS_PAGE = 1;

export const Home: FC<RouteComponentProps> = ({ history }) => {
    const { data, loading } = useQuery<ListingsData, ListingsVariables>(
        LISTINGS,
        {
            variables: {
                filter: ListingsFilter.PRICE_HIGH_TO_LOW,
                limit: PREMIUM_LISTINGS_LIMIT,
                page: PREMIUM_LISTINGS_PAGE,
            },
            fetchPolicy: 'cache-and-network',
        }
    );

    const onSearch = (value: string) => {
        const trimmed = value.trim();
        if (trimmed) history.push(`/listings/${trimmed}`);
        else displayErrorMessage('Please provie a search value!');
    };

    const renderListingsSection = () => {
        if (loading) return <HomeListingsSkeleton />;

        if (data) {
            return (
                <HomeListings
                    title="Premium Listings"
                    listings={data.listings.result}
                />
            );
        }

        return null;
    };

    return (
        <Content
            className="home"
            style={{ backgroundImage: `url(${mapBackgroud})` }}
        >
            <HomeHero onSearch={onSearch} />

            <div className="home__cta-section">
                <Title level={2} className="home__cta-section-title">
                    Your guide for all things rental
                </Title>
                <Paragraph>
                    Helping you make the best decisions in renting your last
                    minute locations.
                </Paragraph>
                <Link
                    className="ant-btn ant-btn-primary ant-btn-large home__cta-section-button"
                    to="/listings/united%20states"
                >
                    Popular listings in the United States
                </Link>
            </div>

            {renderListingsSection()}

            <div>
                <Title level={4} className="home__listings-title">
                    Listings of any king
                </Title>
                <Row gutter={12}>
                    <Col xs={24} sm={12}>
                        <Link to="/listings/san%20francisco">
                            <div className="home__listings-img-cover">
                                <img
                                    src={sanFranciscoImage}
                                    alt="San Francisco"
                                    className="home__listings-img"
                                />
                            </div>
                        </Link>
                    </Col>
                    <Col xs={24} sm={12}>
                        <Link to="/listings/cancun">
                            <div className="home__listings-img-cover">
                                <img
                                    src={cancunImage}
                                    alt="Cancun"
                                    className="home__listings-img"
                                />
                            </div>
                        </Link>
                    </Col>
                </Row>
            </div>
        </Content>
    );
};
