import React, { FC } from 'react';
import { Listing } from '../../../../lib';
import { Typography, List, Divider } from 'antd';
import Avatar from 'antd/lib/avatar/avatar';
import { Link } from 'react-router-dom';

interface Props {
    listingBookings: Listing['listing']['bookings'];
    bookingsPage: number;
    limit: number;
    setBookingsPage: (page: number) => void;
}

const { Text, Title } = Typography;

export const ListingBookings: FC<Props> = ({
    listingBookings,
    bookingsPage,
    limit,
    setBookingsPage,
}) => {
    const total = listingBookings?.total;
    const result = listingBookings?.result;

    const listingBookingsList = listingBookings && (
        <List
            grid={{ gutter: 8, xs: 1, sm: 2, md: 2, lg: 3, xl: 3, xxl: 3 }}
            dataSource={result}
            locale={{ emptyText: 'No bookings have been made yet!' }}
            pagination={{
                current: bookingsPage,
                total,
                defaultPageSize: limit,
                hideOnSinglePage: true,
                showLessItems: true,
                onChange: (page: number) => setBookingsPage(page),
            }}
            renderItem={(listingBooking) => {
                const bookingHistory = (
                    <div className="user-bookings__booking-history">
                        <div>
                            Check in:{' '}
                            <Text strong>{listingBooking.checkIn}</Text>
                        </div>
                        <div>
                            Check out:{' '}
                            <Text strong>{listingBooking.checkOut}</Text>
                        </div>
                    </div>
                );

                return (
                    <List.Item className="listing-bookings__item">
                        {bookingHistory}
                        <Link to={`/user/${listingBooking.tenant.id}`}>
                            <Avatar src={listingBooking.tenant.avatar} />
                        </Link>
                    </List.Item>
                );
            }}
        />
    );

    const userBookingsElement = listingBookingsList ? (
        <div className="user-bookings">
            <Divider />
            <div className="listing-bookings__section">
                <Title level={4} className="user-listings__title">
                    Bookings
                </Title>
            </div>
            {listingBookingsList}
        </div>
    ) : null;

    return <>{userBookingsElement}</>;
};
