import React, { FC } from 'react';
import { Card, Typography } from 'antd';
import { UserOutlined } from '@ant-design/icons';
import { formatListingPrice, iconColor } from '../../utils';
import { Link } from 'react-router-dom';

interface Props {
    listing: {
        id: string;
        title: string;
        image: string;
        address: string;
        price: number;
        numOfGuests: number;
    };
}
const { Text, Title } = Typography;
export const ListingCard: FC<Props> = ({
    listing: { id, title, image, address, price, numOfGuests },
}) => {
    return (
        <Link to={`/listing/${id}`}>
            <Card
                hoverable
                cover={
                    <div
                        style={{ backgroundImage: `url(${image})` }}
                        className="listing-card__cover-img"
                    />
                }
            >
                <div>
                    <div className="listing-card__description">
                        <Title level={4} className="listing-card__price">
                            {formatListingPrice(price)}
                            <span>/day</span>
                        </Title>
                        <Text strong ellipsis className="listing-card__title">
                            {title}
                        </Text>
                        <Text strong ellipsis className="listing-card__address">
                            {address}
                        </Text>
                    </div>
                    <div className="listing-card__dimensions listing-card__dimensions--guests">
                        <UserOutlined style={{ color: iconColor }} />{' '}
                        <Text>{numOfGuests} guests</Text>
                    </div>
                </div>
            </Card>
        </Link>
    );
};
