import React, { FC } from 'react';
import {
    User as UserData,
    formatListingPrice,
    DISCONNECT_STRIPE,
    DisconnectStripe as DisconnectStripeData,
    Viewer,
    displaySuccessNotification,
    displayErrorMessage,
} from '../../../../lib';
import { Typography, Card, Divider, Button, Tag } from 'antd';
import Avatar from 'antd/lib/avatar/avatar';
import { useMutation } from 'react-apollo';

interface Props {
    user: UserData['user'];
    viewerIsUser: boolean;
    viewer: Viewer;
    setViewer: (viewer: Viewer) => void;
    handleUserRefetch: () => void;
}

const stripeAuthUrl = `https://connect.stripe.com/oauth/authorize?response_type=code&client_id=${process.env.REACT_APP_S_CLIENT_ID}&scope=read_write`;

const { Paragraph, Text, Title } = Typography;
export const UserProfile: FC<Props> = ({
    user,
    viewerIsUser,
    viewer,
    setViewer,
    handleUserRefetch,
}) => {
    const [disconnectStripe, { loading }] = useMutation<DisconnectStripeData>(
        DISCONNECT_STRIPE,
        {
            onCompleted: (data) => {
                if (data?.disconnectStripe) {
                    setViewer({
                        ...viewer,
                        hasWallet: data.disconnectStripe.hasWallet,
                    });
                    displaySuccessNotification(
                        "You've successfully disconnected from Stripe!",
                        "You'll have to reconnect with Stripe before you continue creating listings"
                    );
                    handleUserRefetch();
                }
            },
            onError: () => {
                displayErrorMessage(
                    "Sorry! We weren't able to disconnect you from Stripe. Please try again later!"
                );
            },
        }
    );

    const redirectToStripe = () => {
        window.location.href = stripeAuthUrl;
    };

    const handleStripeDisconnect = async () => {
        await disconnectStripe();
    };

    const additionalDetails = user.hasWallet ? (
        <>
            <Paragraph>
                <Tag color="green">Stripe Registered</Tag>
            </Paragraph>
            <Paragraph>
                Income Earned:{' '}
                <Text strong>
                    {user.income ? formatListingPrice(user.income) : `$0.00`}
                </Text>
            </Paragraph>
            <Button
                loading={loading}
                type="primary"
                className="user-profile__details-cta"
                onClick={handleStripeDisconnect}
            >
                Disconnect Stripe
            </Button>
            <Paragraph type="secondary">
                By disconnecting, you won't be able to receice{' '}
                <Text strong>any further payments</Text>. This will prevent
                users from booking listings that you might have already created.
            </Paragraph>
        </>
    ) : (
        <>
            <Paragraph>
                Interested in becoming a TinyHouse host? Register with your
                Stripe account!
            </Paragraph>
            <Button
                type="primary"
                className="user-profile__details-cta"
                onClick={redirectToStripe}
            >
                Connect with Stripe
            </Button>
            <Paragraph type="secondary">
                TinyHouse uses{' '}
                <a
                    href="https://stripe.com/en-US/connect"
                    target="_blank"
                    rel="noopener noreferrer"
                >
                    Stripe
                </a>{' '}
                to help transfer your earnings in a secure and trusted manner.
            </Paragraph>
        </>
    );

    const additionalDetailsSection = viewerIsUser && (
        <>
            <Divider />
            <div className="user-profile__details">
                <Title level={4}>Additional Details</Title>
                {additionalDetails}
            </div>
        </>
    );

    return (
        <div className="user-profile">
            <Card className="user-profile__card">
                <div className="user-profile__avatar">
                    <Avatar size={100} src={user.avatar} />
                </div>
                <Divider />
                <div className="user-profile__details">
                    <Title level={4}>Details</Title>
                    <Paragraph>
                        Name: <Text strong>{user.name}</Text>
                    </Paragraph>
                    <Paragraph>
                        Contact: <Text strong>{user.contact}</Text>
                    </Paragraph>
                    {additionalDetailsSection}
                </div>
            </Card>
        </div>
    );
};
