import React, { FC } from 'react';
import { Listings, ListingCard } from '../../../../lib';
import { List, Typography } from 'antd';

interface Props {
    listings: Listings['listings']['result'];
    title: string;
}

const { Title } = Typography;
export const HomeListings: FC<Props> = ({ title, listings }) => {
    return (
        <div className="home-listings">
            <Title level={4} className="home-listings__title">
                {title}
            </Title>
            <List
                grid={{
                    gutter: 8,
                    xs: 1,
                    sm: 2,
                    md: 2,
                    lg: 4,
                    xl: 4,
                    xxl: 4,
                }}
                dataSource={listings}
                renderItem={(listing) => {
                    return (
                        <List.Item>
                            <ListingCard listing={listing} />
                        </List.Item>
                    );
                }}
            />
        </div>
    );
};
