import React, { FC } from 'react';
import { User, ListingCard } from '../../../../lib';
import { Typography, List } from 'antd';

interface Props {
    userListings: User['user']['listings'];
    listingsPage: number;
    limit: number;
    setListingsPage: (page: number) => void;
}

const { Paragraph, Title } = Typography;

export const UserListings: FC<Props> = ({
    userListings,
    listingsPage,
    limit,
    setListingsPage,
}) => {
    const { total, result } = userListings;
    console.log(listingsPage);
    const userListingsList = (
        <List
            grid={{ gutter: 8, xs: 1, sm: 2, md: 2, lg: 4, xl: 4, xxl: 4 }}
            dataSource={result}
            locale={{ emptyText: "User doesn't have any listings yet!" }}
            pagination={{
                style: { marginBottom: 20 },
                position: 'top',
                current: listingsPage,
                total,
                defaultPageSize: limit,
                hideOnSinglePage: true,
                showLessItems: true,
                onChange: (page: number) => setListingsPage(page),
            }}
            renderItem={(userListing) => (
                <List.Item>
                    <ListingCard listing={userListing} />
                </List.Item>
            )}
        />
    );

    return (
        <div>
            <Title level={4} className="user-listings__title">
                Listings
            </Title>
            <Paragraph>
                This section highlights the listings this user currently hosts
                and has made available for bookings.
            </Paragraph>
            {userListingsList}
        </div>
    );
};
