import React, { useState } from 'react';
import { RouteComponentProps } from 'react-router-dom';
import { useQuery } from 'react-apollo';
import { USER, Viewer, PageSkeleton, ErrorBanner } from '../../lib';
import { User as UserData, UserVariables } from '../../lib';
import { Layout, Row, Col } from 'antd';
import { UserProfile } from './components';
import { UserListings } from './components/UserListings';
import { UserBookings } from './components/UserBookings';

interface MatchParams {
    id: string;
}

interface ExtraProps {
    viewer: Viewer;
    setViewer: (viewer: Viewer) => void;
}

const { Content } = Layout;
const PAGE_LIMIT = 4;
export const User = ({
    match,
    viewer,
    setViewer,
}: RouteComponentProps<MatchParams> & ExtraProps) => {
    const [listingsPage, setListingsPage] = useState(1);
    const [bookingsPage, setBookingsPage] = useState(1);

    const { data, error, loading, refetch } = useQuery<UserData, UserVariables>(
        USER,
        {
            variables: {
                id: match.params.id,
                listingsPage,
                bookingsPage,
                limit: PAGE_LIMIT,
            },
            fetchPolicy: 'cache-and-network',
        }
    );

    const user = data ? data.user : null;
    const viewerIsUser = user?.id === viewer.id;

    const userListings = user?.listings;
    const userBookings = user?.bookings;

    const handleUserRefetch = async () => {
        await refetch();
    };

    const userProfileElement = user ? (
        <UserProfile
            user={user}
            viewerIsUser={viewerIsUser}
            viewer={viewer}
            setViewer={setViewer}
            handleUserRefetch={handleUserRefetch}
        />
    ) : null;

    const stripeError = new URL(window.location.href).searchParams.get(
        'stripe_error'
    );

    const stripeErrorBanner = stripeError && (
        <ErrorBanner description="We had an issue connecting with Stripe. Please try again soon." />
    );

    if (loading && !data) {
        return (
            <Content className="user">
                <PageSkeleton />
            </Content>
        );
    }

    if (error) {
        return (
            <Content className="user">
                <ErrorBanner description="This user may not exist or we've encountered an error. Please try again soon." />
                <PageSkeleton />
            </Content>
        );
    }

    const userListingsElement = userListings && (
        <UserListings
            userListings={userListings}
            listingsPage={listingsPage}
            limit={PAGE_LIMIT}
            setListingsPage={setListingsPage}
        />
    );
    const userBookingsElement = userBookings && (
        <UserBookings
            userBookings={userBookings}
            bookingsPage={bookingsPage}
            limit={PAGE_LIMIT}
            setBookingsPage={setBookingsPage}
            viewerIsUser={viewerIsUser}
        />
    );

    return (
        <Content className="user">
            {stripeErrorBanner}
            <Row gutter={12} itemType="flex" justify="space-between">
                <Col xs={24}>{userProfileElement}</Col>
                <Col xs={24}>
                    {userListingsElement}
                    {userBookingsElement}
                </Col>
            </Row>
        </Content>
    );
};
