import React, { FC, useState } from 'react';
import {
    Layout,
    Typography,
    Form,
    Input,
    InputNumber,
    Radio,
    Upload,
    Button,
} from 'antd';
import {
    Viewer,
    iconColor,
    displayErrorMessage,
    HostListing as HostListingData,
    HostListingVariables,
    HOST_LISTING,
    displaySuccessNotification,
} from '../../lib';
import { Link, Redirect } from 'react-router-dom';
import { ListingType } from '../../lib/graphql/globalTypes';
import {
    BankOutlined,
    HomeOutlined,
    LoadingOutlined,
    PlusOutlined,
} from '@ant-design/icons';
import { RcFile } from 'antd/lib/upload';
import { UploadChangeParam } from 'antd/lib/upload';
import { useForm } from 'antd/lib/form/Form';
import { useMutation } from 'react-apollo';

interface Props {
    viewer: Viewer;
    setViewer: (viewer: Viewer) => void;
}

const { Item } = Form;
const { Content } = Layout;
const { Text, Title } = Typography;
export const Host: FC<Props> = ({ setViewer, viewer }) => {
    const [hostListing, { loading, data }] = useMutation<
        HostListingData,
        HostListingVariables
    >(HOST_LISTING, {
        onCompleted: () => {
            displaySuccessNotification(
                "You've successfully created your listing!"
            );
        },
        onError: () => {
            displayErrorMessage(
                "Sorry! we weren't able to create your listing, please try again later."
            );
        },
    });

    const [form] = useForm();

    const [imageLoading, setImageLoading] = useState(false);
    const [imageBase64Value, setImageBase64Value] = useState<string | null>(
        null
    );
    const beforeUpload = (file: RcFile): boolean => {
        const fileIsValidImage =
            file.type === 'image/jpeg' || file.type === 'image/png';
        const fileIsValidSize = file.size / 1024 / 1024 < 1;

        if (!fileIsValidImage) {
            displayErrorMessage(
                "You're only able to upload valid JPG or PNG files."
            );
            return false;
        }

        if (!fileIsValidSize) {
            displayErrorMessage(
                "You're only able to upload valid image files of under 1MB in size."
            );
            return false;
        }

        return fileIsValidSize && fileIsValidSize;
    };

    const handleImageUpload = (info: UploadChangeParam) => {
        const { file } = info;

        if (file.status === 'uploading') {
            setImageLoading(true);
            return;
        }
        if (file.status === 'done' && file.originFileObj) {
            getBase64Value(file.originFileObj, (imageBase64Value) => {
                setImageBase64Value(imageBase64Value);
                setImageLoading(false);
            });
        }
    };

    if (!viewer.id || !viewer.hasWallet) {
        return (
            <Content className="host-content">
                <div className="host__form-header">
                    <Title level={3} className="host__form-title">
                        You'll have to be signed in and connected with Stripe to
                        host a listing!
                    </Title>
                    <Text type="secondary">
                        We only allow users who've signed in to our application
                        and have connected with Stripe to host new listings. You
                        can sign in by clicking <Link to="/login">here</Link>,
                        you can connect to your Stripe account shortly after.
                    </Text>
                </div>
            </Content>
        );
    }

    const onFinish = (values: any) => {
        const fullAddress = `${values.address}, ${values.city}, ${values.state}, ${values.postalCode}`;

        const input = {
            ...values,
            address: fullAddress,
            image: imageBase64Value,
            price: values.price * 100,
        };

        delete input.city;
        delete input.zip;
        delete input.state;
        delete input.postalCode;

        hostListing({
            variables: {
                input,
            },
        });
    };

    const onFinishFailed = () => {
        displayErrorMessage('Please complete all required form fields!');
    };

    if (loading) {
        return (
            <Content className="host-content">
                <div className="host__form-header">
                    <Title level={3} className="host__form-title">
                        Please wait!
                    </Title>
                    <Text type="secondary">
                        We're creating your listing now.
                    </Text>
                </div>
            </Content>
        );
    }

    if (data) {
        return <Redirect to={`/listing/${data.hostListing.id}`} />;
    }

    return (
        <Content className="host-content">
            <Form
                layout="vertical"
                form={form}
                onFinish={onFinish}
                onFinishFailed={onFinishFailed}
            >
                <div className="host__form-header">
                    <Title level={3} className="host__form-title">
                        Hi! Let's get started listing your place.
                    </Title>
                    <Text type="secondary">
                        In this form, we'll collect some information about your
                        listing.
                    </Text>
                </div>

                <Item
                    label="Home Type"
                    extra="All prices in $USD/day"
                    name="type"
                    rules={[
                        {
                            required: true,
                            message: 'Please select a home type!',
                        },
                    ]}
                >
                    <Radio.Group>
                        <Radio.Button value={ListingType.APARTMENT}>
                            <BankOutlined style={{ color: iconColor }} />{' '}
                            <span>Apartment</span>
                        </Radio.Button>
                        <Radio.Button value={ListingType.HOUSE}>
                            <HomeOutlined style={{ color: iconColor }} />{' '}
                            <span>House</span>
                        </Radio.Button>
                    </Radio.Group>
                </Item>

                <Item
                    name="numOfGuests"
                    label="Max # of Guests"
                    rules={[
                        {
                            required: true,
                            message: 'Please enter the max number of guests!',
                        },
                    ]}
                >
                    <InputNumber min={1} placeholder="4" />
                </Item>

                <Item
                    name="title"
                    label="Title"
                    extra="Max character count of 45"
                    rules={[
                        {
                            required: true,
                            message: 'Please give a title for your listing!',
                        },
                    ]}
                >
                    <Input
                        maxLength={45}
                        placeholder="The iconic and luxurious Bel-Air mansion"
                    />
                </Item>

                <Item
                    name="description"
                    label="Description"
                    extra="Max character count of 400"
                    rules={[
                        {
                            required: true,
                            message:
                                'Please give a description for your listing!',
                        },
                    ]}
                >
                    <Input.TextArea
                        rows={3}
                        maxLength={1024}
                        placeholder="Modern, clean, and iconic home of the Fresh Price. Situated in the heart of Bel-Air Los Angeles."
                    />
                </Item>

                <Item
                    name="address"
                    label="Address"
                    rules={[
                        {
                            required: true,
                            message: 'Please state the address of the listing!',
                        },
                    ]}
                >
                    <Input placeholder="251 North Bristol Avenue" />
                </Item>

                <Item
                    name="city"
                    label="City/Town"
                    rules={[
                        {
                            required: true,
                            message:
                                'Please state the city/town of the listing!',
                        },
                    ]}
                >
                    <Input placeholder="Los Angeles" />
                </Item>

                <Item
                    name="state"
                    label="State/Province"
                    rules={[
                        {
                            required: true,
                            message:
                                'Please state the state/province of the listing!',
                        },
                    ]}
                >
                    <Input placeholder="California" />
                </Item>

                <Item
                    name="postalCode"
                    label="ZIP/Postal Code"
                    rules={[
                        {
                            required: true,
                            message:
                                'Please state the zip/postal code of the listing!',
                        },
                    ]}
                >
                    <Input placeholder="Please enter a zip code for your listing" />
                </Item>

                <Item
                    name="image"
                    label="Image"
                    extra="Images have to be under 1MB in size and of type JPG or PNG"
                    rules={[
                        {
                            required: true,
                            message: 'Please select an image for your listing!',
                        },
                    ]}
                >
                    <div className="host__form-image-upload">
                        <Upload
                            name="image"
                            listType="picture-card"
                            showUploadList={false}
                            action="https://www.mocky.io/v2/5cc8019d300000980a055e76"
                            beforeUpload={beforeUpload}
                            onChange={handleImageUpload}
                        >
                            {imageBase64Value ? (
                                <img src={imageBase64Value} alt="Listing" />
                            ) : (
                                <div>
                                    {imageLoading ? (
                                        <LoadingOutlined />
                                    ) : (
                                        <PlusOutlined />
                                    )}
                                    <div className="ant-upload-text">
                                        Upload
                                    </div>
                                </div>
                            )}
                        </Upload>
                    </div>
                </Item>

                <Item
                    name="price"
                    label="Price"
                    extra="All prices in $USD/day"
                    rules={[
                        {
                            required: true,
                            message: 'Please enter a price for your listing!',
                        },
                    ]}
                >
                    <InputNumber min={0} placeholder="120" />
                </Item>

                <Item>
                    <Button type="primary" block htmlType="submit">
                        Submit
                    </Button>
                </Item>
            </Form>
        </Content>
    );
};

const getBase64Value = (
    img: File | Blob,
    callback: (imageBase64Value: string) => void
) => {
    const reader = new FileReader();
    reader.readAsDataURL(img);
    reader.onload = () => {
        callback(`${reader.result}`);
    };
};
