import React, { FC } from 'react';
import { User, ListingCard } from '../../../../lib';
import { Typography, List } from 'antd';
import Text from 'antd/lib/typography/Text';

interface Props {
    userBookings: User['user']['bookings'];
    bookingsPage: number;
    limit: number;
    setBookingsPage: (page: number) => void;
    viewerIsUser: boolean;
}

const { Paragraph, Title } = Typography;

export const UserBookings: FC<Props> = ({
    userBookings,
    bookingsPage,
    limit,
    setBookingsPage,
    viewerIsUser,
}) => {
    const total = userBookings?.total;
    const result = userBookings?.result;
    console.log(bookingsPage);
    const userBookingsList = userBookings && viewerIsUser && (
        <List
            grid={{ gutter: 8, xs: 1, sm: 2, md: 2, lg: 4, xl: 4, xxl: 4 }}
            dataSource={result}
            locale={{ emptyText: "You haven't made any bookings!" }}
            pagination={{
                style: { marginBottom: 20 },
                position: 'top',
                current: bookingsPage,
                total,
                defaultPageSize: limit,
                hideOnSinglePage: true,
                showLessItems: true,
                onChange: (page: number) => setBookingsPage(page),
            }}
            renderItem={(userBooking) => {
                const bookingHistory = (
                    <div className="user-bookings__booking-history">
                        <div>
                            Check in: <Text strong>{userBooking.checkIn}</Text>
                        </div>
                        <div>
                            Check out:{' '}
                            <Text strong>{userBooking.checkOut}</Text>
                        </div>
                    </div>
                );

                return (
                    <List.Item>
                        {bookingHistory}
                        <ListingCard listing={userBooking.listing} />
                    </List.Item>
                );
            }}
        />
    );

    const userBookingsElement = userBookingsList ? (
        <div>
            <Title level={4} className="user-listings__title">
                Bookings
            </Title>
            <Paragraph>
                This section highlights the bookings you've made, and the
                check-in/check-out dates associated with said bookings.
            </Paragraph>
            {userBookingsList}
        </div>
    ) : null;

    return <>{userBookingsElement}</>;
};
