import React, { useEffect, useRef, FC } from 'react';
import { Card, Layout, Typography, Spin } from 'antd';
//image assets
import googleLogo from './assets/google.png';
import {
    Viewer,
    AUTH_URL,
    AuthUrl,
    LOG_IN,
    LogIn as LogInData,
    LogInVariables,
    ErrorBanner,
    displaySuccessNotification,
} from '../../lib';
import { useApolloClient, useMutation } from 'react-apollo';
import { Redirect } from 'react-router';

const { Content } = Layout;
const { Text, Title } = Typography;

interface Props {
    setViewer: (viewer: Viewer) => void;
}

export const Login: FC<Props> = ({ setViewer }) => {
    const client = useApolloClient();
    const [
        logIn,
        { data: logInData, loading: logInLoading, error: logInError },
    ] = useMutation<LogInData, LogInVariables>(LOG_IN, {
        onCompleted: (data) => {
            if (data?.logIn) {
                setViewer(data.logIn);
                displaySuccessNotification("You've successfully logged in!");
                if (data.logIn.token)
                    sessionStorage.setItem('token', data.logIn.token);
            }
        },
    });

    const logInRef = useRef(logIn);

    useEffect(() => {
        const code = new URL(window.location.href).searchParams.get('code');
        console.log(code);
        if (code) {
            logInRef.current({
                variables: {
                    input: { code },
                },
            });
        }
    }, []);

    const handleAuthorize = async () => {
        try {
            const { data } = await client.query<AuthUrl>({
                query: AUTH_URL,
            });
            window.location.href = data.authUrl;
        } catch (e) {
            displaySuccessNotification(
                "Sorry! We weren't able to log you in. Please try again later!"
            );
        }
    };
    const logInErrorBannerElement = logInError ? (
        <ErrorBanner description="Sorry! We weren't able to log you in. Please try again later" />
    ) : null;

    if (logInLoading) {
        return (
            <Content className="log-in">
                <Spin size="large" tip="Logging you in now..." />
            </Content>
        );
    }
    if (logInData?.logIn) {
        const { id } = logInData.logIn;
        return <Redirect to={`/user/${id}`} />;
    }

    return (
        <Content className="log-in">
            {logInErrorBannerElement}
            <Card className="log-in-card">
                <div>
                    <Title level={3} className="log-in-card__intro-title">
                        <span role="img" aria-label="wave">
                            🖐️
                        </span>
                    </Title>
                    <Title level={3} className="log-in-card__intro-title">
                        Log in to TinyHouse!
                    </Title>
                    <Text>
                        Sign in with Google to start booking available rentals!
                    </Text>
                </div>
                <button
                    className="log-in-card__google-button"
                    onClick={handleAuthorize}
                >
                    <div
                        style={{
                            backgroundColor: '#fff',
                            borderRadius: 2,
                        }}
                    >
                        <img
                            src={googleLogo}
                            alt="Google Logo"
                            className="log-in-card__google-button-logo"
                        />
                    </div>
                    <span className="log-in-card__google-button-text">
                        Sign in with Google
                    </span>
                </button>
                <Text type="secondary">
                    Note: By signing in, you'll be redirected to the Google
                    consent form to sign in with your Google account.
                </Text>
            </Card>
        </Content>
    );
};
