import React, { FC, useEffect, useState } from 'react';
import { Layout, Input } from 'antd';
import logo from './assets/tinyhouse-logo.png';
import { Link, useHistory, useRouteMatch, match } from 'react-router-dom';
import { MenuItems } from './components';
import { Viewer, displayErrorMessage } from '../../lib';

const { Header } = Layout;
const { Search } = Input;

interface Props {
    viewer: Viewer;
    setViewer: (viewer: Viewer) => void;
}

export const AppHeader: FC<Props> = ({ viewer, setViewer }) => {
    const [val, setVal] = useState('');
    const history = useHistory();
    const routeMatch: match<{ location: string }> | null = useRouteMatch(
        '/listings/:location?'
    );

    const onSearch = (val: string) => {
        const trimmedValue = val.trim();

        if (trimmedValue) {
            history.push(`/listings/${trimmedValue}`);
        } else {
            displayErrorMessage('Please enter a valid search!');
        }
    };
    const location = routeMatch?.params.location;
    useEffect(() => {
        setVal(location ? location : '');
    }, [location]);

    return (
        <Header className="app-header">
            <div className="app-header__logo-search-section">
                <div className="app-header__logo">
                    <Link to="/">
                        <img src={logo} alt="App Logo" />
                    </Link>
                </div>
                <div className="app-header-skeleton__search-input">
                    <Search
                        placeholder="Search 'San Francisco'"
                        enterButton
                        onSearch={onSearch}
                        value={val}
                        onChange={(e) => setVal(e.target.value)}
                    />
                </div>
            </div>
            <div className="app-header__menu-section">
                <MenuItems viewer={viewer} setViewer={setViewer} />
            </div>
        </Header>
    );
};
